export default {
    data() {
        return {
            // 服务器地址，供上传组件调用，应与 axios 的配置一致
            baseURL: process.env.VUE_APP_API_URL,
            // 数据列表
            list: []
        }
    },
    methods: {
        async getList() {
            const res = await this.$http.get(`/api/${this.modelName}`)
            this.list = res.data
        },
        remove(name, id) {
            this.$confirm(`确认删除 ${name}？`, {
                type: 'warning'
            })
            .then(async () => {
                await this.$http.delete(`/api/${this.modelName}/${id}`)
                // 刷新列表数据
                this.getList()
            })
            .catch(() => {})
        }
    },
    created() {
        this.getList()
    },
}