export default {
    data() {
        return {
            // 服务器地址，供上传组件调用，应与 axios 的配置一致
            baseURL: process.env.VUE_APP_API_URL,
            // 要修改的数据 ID，为空则说明是创建
            editId: this.$route.params.id || '',
        }
    },
    computed: {
        // 判断当前组件是用于创建还是修改
        isCreated() {
            return this.editId === ''
        }
    },
    methods: {
        // 编辑时的数据回填
        async refill() {
            const res = await this.$http.get(`/api/${this.modelName}/${this.editId}`)
            this.model = res.data
        },
        // 提交表单
        onSubmit() {
            // 验证表单
            this.$refs.form.validate(async valid => {
                if (valid) {
                    // 根据当前组件是新建还是修改作出相应操作
                    if (this.isCreated) {
                        await this.$http.post(`/api/${this.modelName}`, this.model)
                    } else {
                        await this.$http.put(`/api/${this.modelName}`, this.model)
                    }
                    this.$router.push(`/${this.modelName}/list`)
                } else {
                    return false
                }
            })
        },
        // 用于设置上传时的请求头，携带 token 信息
        getAuthHeaders() {
            return { Authorization: localStorage.token || ''}
        }
    },
    created() {
        // 如果不是创建，则需要获取当前信息，用于表单回填
        !this.isCreated && this.refill()
    }
}