import axios from 'axios'
import Vue from 'vue'
import vue from '../main'

axios.defaults.baseURL = process.env.VUE_APP_API_URL

// 使用拦截器统一处理请求
axios.interceptors.request.use(config => {
    // 为请求头添加 Authorization 参数，设置为 token，这样每次发送请求时，都会携带 token
    config.headers.Authorization = localStorage.getItem('token') || ''
    // 返回配置参数，供 ajax 方法继续使用
    return config
}, err => {
    // 处理请求错误
})

// 使用拦截器统一处理响应
axios.interceptors.response.use(res => {
    // 仅保留 data 部分，包含了后端在 data 中保存的错误码、错误信息等
    const data = res.data
    // 0: 请求成功
    if (data.status === 0 && data.message && data.message != '查询成功') {
        Vue.prototype.$message.success(data.message)
    }
    // 1: 未知错误, 2: 权限不足
    if (data.status === 1) {
        Vue.prototype.$message.error(data.message)
    }
    if (data.status === 2) {
        Vue.prototype.$message.error(data.message)
        vue.$router.push('/login').catch(err => {})
    }
    // 把数据返回，交给 ajax 方法接收
    return data
}, err => {
    // 非 2xx 的状态码统一处理
    Vue.prototype.$message.error('服务器开小差了，稍后再试吧')
})

export default axios