<template>
    <div>
        <h2>{{ isCreated ? '发布文章' : '编辑文章' }}</h2>
        <el-form :model="model" :rules="rules" label-width="80px" ref="form">
            <el-form-item label="标题" prop="title">
                <el-input v-model="model.title"></el-input>
            </el-form-item>
            <el-form-item label="发布日期" prop="date">
                <el-date-picker placeholder="请选择发布日期" type="date" v-model="model.date" value-format="yyyy-MM-dd"></el-date-picker>
            </el-form-item>
            <el-form-item label="分类" prop="category">
                <el-radio
                    :key="category"
                    :label="category"
                    v-for="category in categories"
                    v-model="model.category"
                >{{ category }}</el-radio>
            </el-form-item>
            <el-form-item label="内容" prop="content">
                <div id="editor"></div>
            </el-form-item>
            <el-form-item>
                <el-button @click="$router.push('/article/list')" plain type="info">返回</el-button>
                <el-button @click="onSubmit()" type="primary">提交</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import E from 'wangeditor'
    import editMixin from '../mixin/edit'

    export default {
        name: 'ArticleEdit',
        mixins: [editMixin],
        data() {
            return {
                modelName: 'article',
                // 发送到后端的数据
                model: {
                    title: '',
                    date: new Date(),
                    category: '',
                    content: ''
                },
                // 验证规则
                rules: {
                    title: [{ required: true, message: '请输入文章标题', trigger: 'blur' }],
                    date: [{ required: true, message: '请选择发布日期', trigger: 'blur' }],
                    category: [{ required: true, message: '请选择分类', trigger: 'blur' }]
                },
                // 文章分类
                categories: ['公告', '活动', '新闻']
            }
        },
        methods: {
            // 由于需要操作富文本编辑器，所以覆盖了 mixin 中的方法
            onSubmit() {
                // 先获取 wangEditor 的数据保存到 model 中，这并不是一个组件，因此暂时没法实现双向绑定
                this.model.content = this.editor.txt.html()
                // 验证表单
                this.$refs.form.validate(async valid => {
                    if (valid) {
                        // 根据当前组件是新建还是修改作出相应操作
                        if (this.isCreated) {
                            await this.$http.post(`/api/${this.modelName}`, this.model)
                        } else {
                            await this.$http.put(`/api/${this.modelName}`, this.model)
                        }
                        this.$router.push(`/${this.modelName}/list`)
                    } else {
                        return false
                    }
                })
            },
            // 编辑时的数据回填，同样覆盖了 mixin 中的方法
            async refill() {
                const res = await this.$http.get(`/api/${this.modelName}/${this.editId}`)
                this.model = res.data
                // 回填时也要先设置 wangEditor 的内容，啊~~没有双向绑定好麻烦
                this.editor.txt.html(res.data.content)
            },
        },
        mounted() {
            this.editor = new E('#editor')
            this.editor.create()
        }
    }
</script>

<style lang="scss">
    // 日期选择器的层叠优先级，不然会被富文本编辑器按钮挡住！富文本编辑器的 z-index 是 10001
    .el-picker-panel {
        z-index: 20007 !important;
    }
</style>
