<template>
    <div>
        <h2>世界观列表</h2>
        <el-table :data="list" stripe style="width: 100%">
            <el-table-column prop="pic" label="图片" width="100">
                <template v-slot="scope">
                    <img :src="baseURL + scope.row.pic" style="width: 60px;" />
                </template>
            </el-table-column>
            <el-table-column prop="title" label="标题" width="100"></el-table-column>
            <el-table-column prop="engName" label="英文名称" width="150"></el-table-column>
            <el-table-column prop="description" label="描述"></el-table-column>
            <el-table-column fixed="right" label="操作" width="200">
                <template v-slot="scope">
                    <!-- Element-UI 通过作用域插槽传递了表示当前行的 scope，通过 scope.row 可以获取当前行数据 -->
                    <el-button type="primary" size="small" @click="$router.push(`/story/edit/${scope.row._id}`)">编辑</el-button>
                    <el-button type="danger" size="small" @click="remove(scope.row.title, scope.row._id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import listMixin from '../mixin/list'

    export default {
        name: 'StoryList',
        mixins: [listMixin],
        data() {
            return {
                modelName: 'story',
            }
        },
    }
</script>

