<template>
    <div id="login">
        <el-card class="box-card">
            <div slot="header">
                <span>管理员登录</span>
            </div>
            <el-form :model="model" :rules="rules" ref="form" style="margin-top: 0">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model="model.username"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input @keypress.native.enter="onSubmit" type="password" v-model="model.password"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button @click="onSubmit" type="primary">登录</el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
    export default {
        name: 'Login',
        data() {
            return {
                model: {
                    username: '',
                    password: ''
                },
                // 验证规则
                rules: {
                    username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
                    password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
                },
            }
        },
        methods: {
            // 提交表单
            onSubmit() {
                // 验证表单
                this.$refs.form.validate(async valid => {
                    if (valid) {
                        const res = await this.$http.post('/api/login', this.model)
                        // 如果登录成功
                        if (res && res.status === 0) {
                            // 保存 token
                            localStorage.setItem('token', res.data.token)
                            this.$router.push('/character/list')
                        }
                    } else {
                        return false
                    }
                })
            },
        }
    }
</script>

<style lang="scss" scoped>
    #login {
        display: flex;
        justify-content: center;
        padding-top: 200px;

        .box-card {
            width: 500px;
        }
    }
</style>
