import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import CharacterEdit from '../views/CharacterEdit.vue'
import CharacterList from '../views/CharacterList.vue'
import StoryEdit from '../views/StoryEdit.vue'
import StoryList from '../views/StoryList.vue'
import ArticleEdit from '../views/ArticleEdit.vue'
import ArticleList from '../views/ArticleList.vue'
import UserEdit from '../views/UserEdit.vue'
import UserList from '../views/UserList.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/login',
        name: 'Login',
        component: Login,
        meta: { isPublic: true }
    },
    {
        path: '/',
        name: 'Home',
        component: Home,
        children: [
            { path: '/character/create', component: CharacterEdit },
            { path: '/character/edit/:id', component: CharacterEdit },
            { path: '/character/list', component: CharacterList },

            { path: '/story/create', component: StoryEdit },
            { path:  '/story/edit/:id', component: StoryEdit },
            { path: '/story/list', component: StoryList },

            { path: '/article/create', component: ArticleEdit },
            { path:  '/article/edit/:id', component: ArticleEdit },
            { path: '/article/list', component: ArticleList },

            { path: '/user/create', component: UserEdit },
            { path:  '/user/edit/:id', component: UserEdit },
            { path: '/user/list', component: UserList },
        ]
    }
]

const router = new VueRouter({
    routes
})

// 如果页面不是公开的，并且没有登录，则拦截到登录页面
router.beforeEach((to, from, next) => {
    if (!to.meta.isPublic && !localStorage.getItem('token')) {
        return router.push('/login').catch(err => {})
    }
    next()
})

export default router