<template>
    <el-container>
        <el-aside width="200px">
            <!-- default-active 绑定当前路径可以实现高亮，直接获取即可，不用监听路由！ -->
            <el-menu :default-active="$route.path" router>
                <el-submenu index="1">
                    <template slot="title">
                        <i class="el-icon-message"></i>内容管理
                    </template>
                    <el-menu-item-group>
                        <template slot="title">干员</template>
                        <el-menu-item index="/character/create">新建干员</el-menu-item>
                        <el-menu-item index="/character/list">干员列表</el-menu-item>
                    </el-menu-item-group>
                    <el-menu-item-group>
                        <template slot="title">世界观</template>
                        <el-menu-item index="/story/create">新建世界观</el-menu-item>
                        <el-menu-item index="/story/list">世界观列表</el-menu-item>
                    </el-menu-item-group>
                    <el-menu-item-group>
                        <template slot="title">文章</template>
                        <el-menu-item index="/article/create">发布文章</el-menu-item>
                        <el-menu-item index="/article/list">文章列表</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
                <el-submenu index="2">
                    <template slot="title">
                        <i class="el-icon-setting"></i>系统管理
                    </template>
                    <el-menu-item-group>
                        <template slot="title">用户</template>
                        <el-menu-item index="/user/create">新建用户</el-menu-item>
                        <el-menu-item index="/user/list">用户列表</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
            </el-menu>
        </el-aside>

        <el-container style="min-width: 1200px;">
            <el-header>
                <el-dropdown>
                    <i class="el-icon-setting" style="margin-right: 15px"></i>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native="changePwd">修改密码</el-dropdown-item>
                        <el-dropdown-item @click.native="logout">退出登录</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
                <span>{{ user.username }}</span>
            </el-header>

            <el-main>
                <!-- 由于新建和编辑共用一个组件，为了在切换路由时清除组件原有的数据，可以为 <router-view> 添加 key 属性  -->
                <router-view :key="$route.fullPath"></router-view>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
    export default {
        name: 'Home',
        data() {
            return {
                user: {}
            }
        },
        methods: {
            changePwd() {
                this.$router.push(`/user/edit/${this.user._id}`)
            },
            logout() {
                localStorage.clear('token')
                this.$router.push('/login')
            },
            async getUser() {
                let res = await this.$http.get(`/api/usertoken/${localStorage.token}`)
                this.user = res.data
            }
        },
        created() {
            this.getUser()
        }
    }
</script>

<style lang="scss">
    .el-container {
        height: 100vh;
    }

    .el-header {
        line-height: 60px;
        text-align: right;
        font-size: 12px;
        color: #333;
        background-color: #b3c0d1;
    }

    .el-aside {
        color: #333;
        background-color: rgb(238, 241, 246);
    }
</style>
