<template>
    <div>
        <h2>{{ isCreated ? '新建干员' : '编辑干员' }}</h2>
        <el-form :model="model" :rules="rules" label-width="80px" ref="form">
            <el-form-item label="中文名称" prop="name">
                <el-input v-model="model.name"></el-input>
            </el-form-item>
            <el-form-item label="英文名称" prop="engName">
                <el-input v-model="model.engName"></el-input>
            </el-form-item>
            <el-form-item label="声优" prop="cv">
                <el-input v-model="model.cv"></el-input>
            </el-form-item>
            <el-form-item label="阵营" prop="faction">
                <el-select v-model="model.faction">
                    <el-option
                        :key="faction"
                        :label="faction"
                        :value="faction"
                        v-for="faction in this.factions"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="职业" prop="vocation">
                <el-select v-model="model.vocation">
                    <el-option
                        :key="vocation"
                        :label="vocation"
                        :value="vocation"
                        v-for="vocation in this.vocations"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="立绘">
                <el-upload
                    :action="this.baseURL + 'api/upload'"
                    :headers="getAuthHeaders()"
                    :on-success="uploadSuccess"
                    :show-file-list="false"
                    class="avatar-uploader"
                >
                    <img :src="this.baseURL + model.tachie" class="avatar" v-if="model.tachie" />
                    <i class="el-icon-plus avatar-uploader-icon" v-else></i>
                </el-upload>
            </el-form-item>
            <el-form-item label="描述" prop="description">
                <el-input type="textarea" v-model="model.description"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="$router.push('/character/list')" plain type="info">返回</el-button>
                <el-button @click="onSubmit()" type="primary">提交</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import editMixin from '../mixin/edit'

    export default {
        name: 'CharacterEdit',
        mixins: [editMixin],
        data() {
            return {
                modelName: 'character',
                // 发送到后端的数据
                model: {
                    name: '',
                    engName: '',
                    cv: '',
                    vocation: '',
                    faction: '',
                    tachie: '',
                    description: '',
                },
                // 验证规则
                rules: {
                    name: [{ required: true, message: '请输入干员名称', trigger: 'blur' }],
                    engName: [{ required: true, message: '请输入干员英文名称', trigger: 'blur' }],
                    cv: [{ required: true, message: '请输入干员CV', trigger: 'blur' }],
                    faction: [{ required: true, message: '请选择阵营', trigger: 'blur' }],
                    vocation: [{ required: true, message: '请选择干员职业', trigger: 'blur' }],
                    description: [{ required: true, message: '请输入干员描述', trigger: 'blur' }]
                },
                // 干员职业
                vocations: ['近卫', '医疗', '先锋', '狙击', '术士', '重装', '辅助', '特种'],
                // 阵营
                factions: ['罗德岛', '龙门', '黑钢', '企鹅物流', '莱茵生命']
            }
        },
        methods: {
            // 立绘上传成功后的处理函数
            uploadSuccess(res, file) {
                // 将上传后的图片地址返回给客户端，用于提交到数据库保存
                this.model.tachie = `uploads/${res.data.filename}`
            },
        },
    }
</script>

<style lang="scss" scoped>
    .el-rate {
        padding-top: 10px;
    }
</style>
