<template>
    <div>
        <h2>{{ isCreated ? '新建世界观' : '编辑世界观' }}</h2>
        <el-form :model="model" :rules="rules" label-width="80px" ref="form">
            <el-form-item label="中文名称" prop="title">
                <el-input v-model="model.title"></el-input>
            </el-form-item>
            <el-form-item label="英文名称" prop="engName">
                <el-input v-model="model.engName"></el-input>
            </el-form-item>
            <el-form-item label="图片">
                <el-upload
                    :action="this.baseURL + 'api/upload'"
                    :headers="getAuthHeaders()"
                    :on-success="uploadSuccess"
                    :show-file-list="false"
                    class="avatar-uploader"
                >
                    <img :src="this.baseURL + model.pic" class="avatar" v-if="model.pic" />
                    <i class="el-icon-plus avatar-uploader-icon" v-else></i>
                </el-upload>
            </el-form-item>
            <el-form-item label="描述" prop="description">
                <el-input type="textarea" v-model="model.description"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="$router.push('/story/list')" plain type="info">返回</el-button>
                <el-button @click="onSubmit()" type="primary">提交</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import editMixin from '../mixin/edit'

    export default {
        name: 'StoryEdit',
        mixins: [editMixin],
        data() {
            return {
                modelName: 'story',
                // 发送到后端的数据
                model: {
                    title: '',
                    engName: '',
                    pic: '',
                    description: '',
                },
                // 验证规则
                rules: {
                    title: [{ required: true, message: '请输入世界观名称', trigger: 'blur' }],
                    engName: [{ required: true, message: '请输入世界观英文名称', trigger: 'blur' }],
                    description: [{ required: true, message: '请输入世界观描述', trigger: 'blur' }]
                },
            }
        },
        methods: {
            // 立绘上传成功后的处理函数
            uploadSuccess(res, file) {
                // 将上传后的图片地址返回给客户端，用于提交到数据库保存
                this.model.pic = `uploads/${res.data.filename}`
            },
        },
    }
</script>
